___CSS_LOADER_EXPORT___.push([module.id, ".nested-enter-active[data-v-fd61d142],.nested-leave-active[data-v-fd61d142]{transition:all .3s ease-in-out}.nested-leave-active[data-v-fd61d142]{transition-delay:.1s}.nested-enter-from[data-v-fd61d142],.nested-leave-to[data-v-fd61d142]{opacity:0;transform:translateX(200px)}.nested-enter-active .inner[data-v-fd61d142],.nested-leave-active .inner[data-v-fd61d142]{transition:all 1s ease-in-out}.nested-enter-active .inner[data-v-fd61d142]{transition-delay:10s}.nested-enter-from .inner[data-v-fd61d142],.nested-leave-to .inner[data-v-fd61d142]{opacity:.001;transform:translateX(30px)}", "",{"version":3,"sources":["webpack://./resources/js/users/Shared/BigCart.vue"],"names":[],"mappings":"AAoIA,4EACE,8BACF,CAGA,sCACE,oBACF,CAEA,sEAGE,SAAU,CADV,2BAEF,CAGA,0FAEE,6BACF,CAGA,6CACE,oBACF,CAEA,oFAQE,YAAc,CANd,0BAOF","sourcesContent":["<template>\n  <transition :duration=\"500\" name=\"nested\">\n    <div v-show=\"opened\" id=\"big-cart\" class=\"py-3\">\n      <div class=\"row mx-0\">\n        <div class=\"col-12 bg-white\">\n          <button class=\"btn bg-transparent fw-normal text-uppercase ms-auto w-auto d-flex px-3 py-3\"\n                  @click=\"switchOpened\"\n          >\n            {{ $t('components.BigCart.close') }}\n          </button>\n        </div>\n      </div>\n\n      <div class=\"row big-cart-items-list mx-0 gy-md-3 gy-4 mt-0\">\n        <div v-if=\"response_products.length > 0\" v-for=\"product in response_products\" class=\"col-12 item\">\n          <div class=\"row\">\n            <div class=\"col-4 col-md-4\">\n              <img :src=\"product.image.url\" alt=\"Item Name\" class=\"img-fluid border-0\">\n            </div>\n            <div class=\"col\">\n              <div class=\"row\">\n                <div class=\"col-12\">\n                  <h5 class=\"item-name\">{{ product.translate[locale].name }}</h5>\n                </div>\n                <div class=\"col-12\">\n                  <span class=\"item-price\">{{ costWithPerfics(product.cost) }}</span>\n                </div>\n\n                <div class=\"col-12 mt-3 fw-light\">\n                  <span class=\"\">{{ $t('components.BigCart.count') }} {{ find(product.id).count }}</span>\n                </div>\n\n                <div class=\"col-12\">\n                  <button class=\"btn bg-transparent px-0 py-3 mt-2 remove-item\" @click=\"remove(product.id)\">\n                    {{ $t('components.BigCart.remove') }}\n                  </button>\n                </div>\n              </div>\n            </div>\n          </div>\n          <hr class=\"mb-0\">\n        </div>\n\n        <div v-if=\"response_products.length > 0\" class=\"col-12\">\n          <div class=\"row justify-content-between\">\n            <div class=\"col-auto\">\n              <span class=\"total-cost\">{{ $t('components.BigCart.total') }}</span>\n            </div>\n\n            <div class=\"col-auto\">\n              <span class=\"total-cost-price\">{{ totalCostWithPrefics }}</span>\n            </div>\n            <div class=\"col-12\">\n              <Link href=\"/order\" @click=\"switchOpened\" id=\"by-order\" class=\"btn btn-dark\">\n                {{ $t('components.BigCart.by-order') }}\n              </Link>\n            </div>\n          </div>\n        </div>\n\n        <div v-if=\"response_products.length === 0\" class=\"col-12\">\n          <span class=\"empty-cart\">\n            {{ $t('components.BigCart.empty-cart') }}\n          </span>\n        </div>\n      </div>\n\n\n    </div>\n  </transition>\n</template>\n\n<script>\nimport {mapActions, mapGetters, mapState} from \"vuex\";\n\nexport default {\n  name: \"BigCart\",\n  emits: ['switchedOpened'],\n  data: () => ({\n    opened: false\n  }),\n  computed: {\n    ...mapGetters('cart', {\n      response_products: \"response_products\",\n      total_cost: 'cost'\n    }),\n    ...mapState('i18n', {\n      locale: 'locale'\n    }),\n    ...mapState('currencies', {\n      currency: 'currency'\n    }),\n    ...mapGetters('cart', {\n      find: 'find'\n    }),\n    user () {\n      return this.$page.props.auth.user\n    },\n    totalCostWithPrefics() {\n      let cost = this.total_cost * this.currency.value\n      cost = cost.toFixed(0)\n      cost = Number(cost)\n      return new Intl.NumberFormat('ru-RU').format(cost) + ' ' + this.currency.symbol\n    }\n  },\n  methods: {\n    switchOpened() {\n      this.opened = !this.opened\n      this.$emit('switchedOpened', this.opened)\n    },\n    cost(cost) {\n      cost = cost * this.currency.value\n      cost = cost.toFixed(0)\n      return new Intl.NumberFormat('ru-RU').format(cost)\n    },\n    costWithPerfics(cost) {\n      return this.cost(cost) + ' ' + this.currency.symbol\n    },\n    ...mapActions('cart', {\n      removeProduct: 'removeProduct'\n    }),\n    remove(id) {\n      this.removeProduct({\n        id: id,\n        user: this.user\n      })\n    }\n  }\n}\n</script>\n\n<style scoped>\n.nested-enter-active, .nested-leave-active {\n  transition: all 0.3s ease-in-out;\n}\n\n/* delay leave of parent element */\n.nested-leave-active {\n  transition-delay: .1s;\n}\n\n.nested-enter-from,\n.nested-leave-to {\n  transform: translateX(200px);\n  opacity: 0;\n}\n\n/* we can also transition nested elements using nested selectors */\n.nested-enter-active .inner,\n.nested-leave-active .inner {\n  transition: all 1s ease-in-out;\n}\n\n/* delay enter of nested element */\n.nested-enter-active .inner {\n  transition-delay: 10s;\n}\n\n.nested-enter-from .inner,\n.nested-leave-to .inner {\n  transform: translateX(30px);\n  /*\n  \tHack around a Chrome 96 bug in handling nested opacity transitions.\n    This is not needed in other browsers or Chrome 99+ where the bug\n    has been fixed.\n  */\n  opacity: 0.001;\n}\n</style>"],"sourceRoot":""}]);
